// Large buttons need to have a somewhat consistent width
.btn-lg {
  min-width: 100px;
}

// Color overrides for small buttons
.btn-xs {
  border: 1px solid @gray-darker;
}
.btn-xs:hover {
  background-color: @gray;
  color: @white;
}

// Overrides for disabled coloring
.btn-default {
  &:hover {
    color: @gray-darker;
    background-color: @gray-lighter;
    border-color: @brand-primary;
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @gray-darker;
    background-color: @gray-lighter;
    border-color: @brand-primary;

    &:hover,
    &:focus,
    &.focus {
      color: @gray-darker;
      background-color: @gray-lighter;
      border-color: @brand-primary;
    }
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: @white;
      border-color: @rain;
       color: @gray-light;
    }
  }
}
.btn-primary {
  &:hover {
    color: @white;
    background-color: @wet-asphalt;
    border-color: @wet-asphalt;
  }
  &:active,
  &.active,
  .open > .dropdown-toggle& {
    color: @white;
    background-color: @wet-asphalt;
    border-color: @wet-asphalt;

    &:hover,
    &:focus,
    &.focus {
      color: @white;
      background-color: @wet-asphalt;
      border-color: @wet-asphalt;
    }
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.focus,
    &:active,
    &.active {
      background-color: @rain;
      border-color: @rain;
    }
  }
}
