th.icon-only,
td.icon-only {
  width: 15px;
}


thead {
  background-color: @gray-light;

  th {
    font-weight: normal;
    padding: 4px 8px!important;
    border-bottom: none!important;
  }
}


tbody {
  // The bootstrap docs suggest using <th scope="row">, which we don't want to be bold
  th {
    font-weight: normal;
    padding: 4px 8px!important;
    border-bottom: none!important;
  }

  tr:first-child th,
  tr:first-child td {
    border-top: none;
  }
}

th.cell-multiline,
td.cell-multiline {
  white-space: pre-line;
}

div.table-btn-container {
  margin-bottom: 8px;
}

tr.highlight {
  background-color: @oasis;
}
