/* Some icons are used in multiple context such as close dialog and table delete
   so we manually add the css for he duplicate ones below */


/* manually updated css which use the same id as other generated css */
.ri-message-confirm,
.message-confirm .ri {
  &:before {
    content: @ri-status-healthy-check;
  }
}
.ri-message-error,
.message-error .ri {
  &:before {
    content: @ri-status-degraded;
  }
}
.ri-checkbox-unselected.ri-checkbox-disabled {
    &:before {
        content: @ri-checkbox-disabled;
    }
}
.ri-checkbox-selected.ri-checkbox-disabled {
    &:before {
        content: @ri-checkbox-selected;
    }
}

.ri-checkbox-indeterminate.ri-checkbox-disabled {
    &:before {
        content: @ri-checkbox-indeterminate;
    }
}


