// Icon Sizes
// -------------------------

/* makes the font 33% larger relative to the icon container */
.@{ri-css-prefix}-lg {
  font-size: 32px;
  line-height: (3em / 4);
  vertical-align: -35%;
}
.@{ri-css-prefix}-sm { font-size: 14px; }
.@{ri-css-prefix}-2x { font-size: 48px; }
.@{ri-css-prefix}-3x { font-size: 72px; }
.@{ri-css-prefix}-4x { font-size: 96px; }
.@{ri-css-prefix}-5x { font-size: 120px; }
