// Rotated & Flipped Icons
// -------------------------

.@{ri-css-prefix}-rotate-90  { .ri-icon-rotate(90deg, 1);  }
.@{ri-css-prefix}-rotate-180 { .ri-icon-rotate(180deg, 2); }
.@{ri-css-prefix}-rotate-270 { .ri-icon-rotate(270deg, 3); }

.@{ri-css-prefix}-flip-horizontal { .ri-icon-flip(-1, 1, 0); }
.@{ri-css-prefix}-flip-vertical   { .ri-icon-flip(1, -1, 2); }

// Hook for IE8-9
// -------------------------

:root .@{ri-css-prefix}-rotate-90,
:root .@{ri-css-prefix}-rotate-180,
:root .@{ri-css-prefix}-rotate-270,
:root .@{ri-css-prefix}-flip-horizontal,
:root .@{ri-css-prefix}-flip-vertical {
  filter: none;
}
