.ri {
  color: @storm;
}

.ri:hover,
span:hover .ri {
  color: @silver;
  cursor: pointer;
}

.modal-header .ri-close,
.modal-body .ri-close {
  float: right;
  margin: 0;
}


/*.ri-table-add {
  font-size: 20px;
  vertical-align: -15%;
}*/

.ri-checkbox-selected,
.ri-checkbox-unselected,
.ri-checkbox-indeterminate,
.ri-radio-selected,
.ri-radio-unselected,
.ri-radio-indeterminate {
  color: @concrete;
}


/*.ri-checkbox-selected:hover, .ri-checkbox-unselected:hover, .ri-checkbox-indeterminate:hover {
  color: @silver;
}*/

.ri-checkbox-selected.ri-checkbox-changed,
.ri-checkbox-unselected.ri-checkbox-changed,
.ri-checkbox-indeterminate.ri-checkbox-changed,
.ri-radio-selected.ri-radio-changed,
.ri-radio-unselected.ri-radio-changed {
  color: @green-sea;
}

.ri-checkbox-selected.ri-checkbox-changed:hover,
.ri-checkbox-unselected.ri-checkbox-changed:hover,
.ri-checkbox-indeterminate.ri-checkbox-changed:hover,
.ri-radio-selected.ri-radio-changed:hover,
.ri-radio-unselected.ri-radio-changed:hover {
  color: @turquoise;
}

.ri-checkbox-disabled,
.ri-checkbox-disabled:hover,
span:hover .ri-checkbox-disabled,
.ri-radio-disabled,
.ri-radio-disabled:hover,
span:hover .ri-radio-disabled {
  color: @platinum;
  cursor: inherit;
}

.ri-status-healthy,
.ri-status-healthy-check,
.ri-status-attention {
  color: @green;
}

.ri-status-healthy:hover,
.ri-status-healthy-check:hover,
.ri-status-attention:hover,
span:hover .ri-status-healthy,
span:hover .ri-status-healthy-check,
span:hover .ri-status-attention {
  color: @emerald;
}

.ri-status-degraded {
  color: @orange;
}

.ri-status-degraded:hover,
span:hover .ri-status-degraded {
  color: @sunflower;
}

.ri-status-critical {
  color: @pomegranate;
}

.ri-status-critical:hover,
span:hover .ri-status-critical {
  color: @tomato;
}

.ri-status-disconnected {
  color: @silver;
}

.ri-status-disconnected:hover,
span:hover .ri-status-disconnected {
  color: @platinum;
}

.ri-save,
.ri-restart {
  color: @green-sea;
}

.ri-save:hover,
.ri-restart:hover,
span:hover .ri-save,
span:hover .ri-restart {
  color: @turquoise;
}

.ri-message-confirm,
.ri-message-confirm:hover,
span:hover .ri-message-confirm {
  color: @green;
  cursor: inherit;
}

.ri-message-error,
.ri-message-error:hover,
span:hover .ri-message-error {
  color: @pomegranate;
  cursor: inherit;
}

/* generic styles class that can override the above */
.ri-disabled,
.ri-disabled:hover,
span:hover .ri-disabled {
  color: @platinum;
  cursor: inherit;
}

.ri-enabled {
  color: @storm;
}

.ri-enabled:hover,
span:hover .ri-enabled {
  color: @silver;
}

.ri-no-interaction:hover,
span:hover .ri-no-interacion {
  color: @storm;
  cursor: inherit;
}

.ri-status-disconnected.ri-no-interaction:hover,
span:hover .ri-status-disconnected.ri-no-interaction {
  color: @silver;
}

