
// Remove the bottom border from the panel heading
.panel-heading {
  border: none;
}

// Increase the size of the panel header font
.panel-title {
  font-size: 22px;
}

.panel-body:first-child {
  padding-top: @theme-panel-body-padding;
}

.panel-body:last-child {
  padding-bottom: @theme-panel-body-padding;
}
