@font-color-h1:           @midnight-blue;
@font-color-h2:           @midnight-blue;
@font-color-h3:           @storm;

h1 {
  color: @font-color-h1;
}

h2 {
  color: @font-color-h2;
}

h3 {
  color: @font-color-h3;
  .h3-data-big {
    font-size: 30px;
    font-weight: bold;
  }
  .h3-data-small {
    font-size: 20px;
    font-weight: bold;
  }
}

.navbar-header,
.nav {
  a {
    border-bottom: none;
  }
  a:hover, a:focus {
    border-bottom: none;
    text-decoration: none;
  }
}

a {
  border-bottom: 1px dashed;
  text-decoration: none;
}

a:hover, a:focus {
  color: @silver;
  border-bottom: 1px solid;
  text-decoration: none;
  cursor: pointer;
}

a > i {
  border-bottom: none;
}

.secondary-body {
  font-size: 12px;
  line-height: 16px;
  color: @concrete;
}

.units {
  font-size: 10px;
}
