.bf-or html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
.bf-or body {
  margin: 0;
}
.bf-or article,
.bf-or aside,
.bf-or details,
.bf-or figcaption,
.bf-or figure,
.bf-or footer,
.bf-or header,
.bf-or hgroup,
.bf-or main,
.bf-or menu,
.bf-or nav,
.bf-or section,
.bf-or summary {
  display: block;
}
.bf-or audio,
.bf-or canvas,
.bf-or progress,
.bf-or video {
  display: inline-block;
  vertical-align: baseline;
}
.bf-or audio:not([controls]) {
  display: none;
  height: 0;
}
.bf-or [hidden],
.bf-or template {
  display: none;
}
.bf-or a {
  background-color: transparent;
}
.bf-or a:active,
.bf-or a:hover {
  outline: 0;
}
.bf-or abbr[title] {
  border-bottom: 1px dotted;
}
.bf-or b,
.bf-or strong {
  font-weight: bold;
}
.bf-or dfn {
  font-style: italic;
}
.bf-or h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
.bf-or mark {
  background: #ff0;
  color: #000;
}
.bf-or small {
  font-size: 80%;
}
.bf-or sub,
.bf-or sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
.bf-or sup {
  top: -0.5em;
}
.bf-or sub {
  bottom: -0.25em;
}
.bf-or img {
  border: 0;
}
.bf-or svg:not(:root) {
  overflow: hidden;
}
.bf-or figure {
  margin: 1em 40px;
}
.bf-or hr {
  box-sizing: content-box;
  height: 0;
}
.bf-or pre {
  overflow: auto;
}
.bf-or code,
.bf-or kbd,
.bf-or pre,
.bf-or samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
.bf-or button,
.bf-or input,
.bf-or optgroup,
.bf-or select,
.bf-or textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
.bf-or button {
  overflow: visible;
}
.bf-or button,
.bf-or select {
  text-transform: none;
}
.bf-or button,
.bf-or html input[type="button"],
.bf-or input[type="reset"],
.bf-or input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
.bf-or button[disabled],
.bf-or html input[disabled] {
  cursor: default;
}
.bf-or button::-moz-focus-inner,
.bf-or input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.bf-or input {
  line-height: normal;
}
.bf-or input[type="checkbox"],
.bf-or input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
.bf-or input[type="number"]::-webkit-inner-spin-button,
.bf-or input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
.bf-or input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: content-box;
}
.bf-or input[type="search"]::-webkit-search-cancel-button,
.bf-or input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
.bf-or fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
.bf-or legend {
  border: 0;
  padding: 0;
}
.bf-or textarea {
  overflow: auto;
}
.bf-or optgroup {
  font-weight: bold;
}
.bf-or table {
  border-collapse: collapse;
  border-spacing: 0;
}
.bf-or td,
.bf-or th {
  padding: 0;
}
@media print {
  .bf-or *,
  .bf-or *:before,
  .bf-or *:after {
    background: transparent !important;
    color: #000 !important;
    box-shadow: none !important;
    text-shadow: none !important;
  }
  .bf-or a,
  .bf-or a:visited {
    text-decoration: underline;
  }
  .bf-or a[href]:after {
    content: " (" attr(href) ")";
  }
  .bf-or abbr[title]:after {
    content: " (" attr(title) ")";
  }
  .bf-or a[href^="#"]:after,
  .bf-or a[href^="javascript:"]:after {
    content: "";
  }
  .bf-or pre,
  .bf-or blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  .bf-or thead {
    display: table-header-group;
  }
  .bf-or tr,
  .bf-or img {
    page-break-inside: avoid;
  }
  .bf-or img {
    max-width: 100% !important;
  }
  .bf-or p,
  .bf-or h2,
  .bf-or h3 {
    orphans: 3;
    widows: 3;
  }
  .bf-or h2,
  .bf-or h3 {
    page-break-after: avoid;
  }
  .bf-or .navbar {
    display: none;
  }
  .bf-or .btn > .caret,
  .bf-or .dropup > .btn > .caret {
    border-top-color: #000 !important;
  }
  .bf-or .label {
    border: 1px solid #000;
  }
  .bf-or .table {
    border-collapse: collapse !important;
  }
  .bf-or .table td,
  .bf-or .table th {
    background-color: #fff !important;
  }
  .bf-or .table-bordered th,
  .bf-or .table-bordered td {
    border: 1px solid #ddd !important;
  }
}
