/* FONT PATH
 * -------------------------- */

 /* --- Make sure to update the URLs for the font from styles.less ---- */

@font-face {
    font-family: 'RiverbedIcon';
    src:    url('@{ri-font-path}/riverbed-icon-webfont.eot?kgrsc8');
    src:    url('@{ri-font-path}/riverbed-icon-webfont.eot?kgrsc8#iefix') format('embedded-opentype'),
            url('@{ri-font-path}/riverbed-icon-webfont.ttf?kgrsc8') format('truetype'),
            url('@{ri-font-path}/riverbed-icon-webfont.woff?kgrsc8') format('woff'),
            url('@{ri-font-path}/riverbed-icon-webfont.svg?kgrsc8#riverbed-icon-webfont') format('svg');
    font-weight: normal;
    font-style: normal;
}

[class^="ri-"], [class*=" ri-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'RiverbedIcon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

