@import "../../colors.less";

//== Global ==
body {
  font-weight: 300;
}

//== List Group
.panel .list-group {
  .list-group-title {
    font-weight: normal;
    font-size: 22px;
  }

  .list-group-item {
    border-width: 0;
    margin: 0;
    padding: 14px 48px;
    color: @concrete;
  }
}

//== Panel
.panel {
  border-width: 0;
}

.panel-default {
  box-shadow: 0px 5px 8px 0px @concrete;
}

.panel-title {
  font-size: 26px;
  color: @storm;
}
